<template>
	<div class="row">
		<div class="col-12">
			<div class="card">
				<div class="card-body">
					<!-- <div class="table-responsive"> -->
					<div id="zero_config_wrapper" class="dataTables_wrapper container-fluid dt-bootstrap4 no-footer">
						<div class="toolbar"></div>
						<div
							class="dataTables_info"
							id="zero_config_info"
							role="status"
							aria-live="polite"
							v-text="pageInfo"
						/>
						<table id="zero_config" class="table table-striped table-bordered">
							<thead>
									<tr>
										<th style="width:10px;">
											<label class="customcheckbox m-b-20">
												<input type="checkbox" id="mainCheckbox" />
												<span class="checkmark"></span>
											</label>
										</th>
									<slot name="head">
										<th
											class="table-head-left"
											v-for="title in titles"
											:key="title"
											v-text="title"
										/>
									</slot>
								</tr>
							</thead>
							<tbody>
								<slot />
							</tbody>
						</table>
						<div class="dataTables_paginate paging_simple_numbers" id="zero_config_paginate">
							<ul class="pagination">
								<li
									:class="['paginate_button', 'page-item', 'previous', {
										disabled: +pagination.Page === 1,
									}]"
									id="zero_config_previous"
								>
									<a
										href="javascript:void(0)"
										class="page-link"
										@click="$emit('onPage', pagination.Page - 1)"
									>上一頁</a>
								</li>
								<li
									v-for="index in pagination.TotalPage"
									:class="['paginate_button', 'page-item', {
										active: index === pagination.Page,
									}]"
									:key="index"
								>
									<a
										href="javascript:void(0)"
										aria-controls="zero_config"
										class="page-link"
										v-text="index"
										@click="$emit('onPage', index)"
									/>
								</li>
								<li
									:class="['paginate_button', 'page-item', 'previous', {
										disabled: +pagination.Page >= +pagination.TotalPage,
									}]"
									id="zero_config_next"
									@click="$emit('onPage', pagination.Page + 1)"
								>
									<a
										href="javascript:void(0)"
										class="page-link"
									>下一頁</a>
								</li>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	name: 'dataTable',
	props: {
		titles: {
			type: [Array, Object],
			default() {
				return []
			},
		},
		pagination: {
			type: [Object],
			default() {
				return {
					// page: 1,
					// totalPage: 1,
					Page: 1,
					PageLimit: 10,
					TotalNumber: 0,
					TotalPage: 1,
				}
			},
		},
	},
    data() {
        return {
        }
    },
    components: {
    },
    methods: {
    },
    created() {
    },
    computed: {
		pageInfo() {
			const { Page, PageLimit, TotalNumber, TotalPage
			} = this.pagination;
			return `顯示第 ${(Page - 1) * PageLimit + 1} 至 ${
				Page >= TotalPage
				? TotalNumber
				: (Page + 0) * PageLimit
			} 項結果，共 ${TotalNumber} 項`
		}
    },
}

</script>

<style scoped>
.table-head-left {
	text-align: left;
}
</style>